.showMovie {
  color: #fff;
  text-shadow: 1px 1px 4px black;
}
.bg {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.container {
  position: absolute;
  min-width: 800px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin: 20px 20px 40px 20;
  padding: 0 20px;
}
.wraper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "a b" "c c";
}
.wraper img {
  box-shadow: 2px 2px 3px 2px gainsboro;
}
.info {
  grid-area: c;
}
.info_etc {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 10px;
  padding: 0 20px;
  grid-template-areas: "a b";
  align-content: center;
  align-items: center;
  justify-items: center;
}
