a {
  text-decoration: none;
  color: #333;
}
li {
  list-style: none;
}
.movie_container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px 20px;
  margin-top: 20px;
}
.movie_wrapper {
  width: 400px;
  text-align: center;
  padding: 15px 10px;
  background-color: aliceblue;
  border-radius: 20px;
}
.movie_wrapper img {
  opacity: 0.7;
  transform: translateY(-30px);
}
.movie_wrapper img:hover {
  opacity: 1;
}
.movie_title {
  height: 80px;
}
.acc {
  font-size: 20px;
  font-weight: 700;
}
.movie_summary {
  height: 130px;
}
.summary_content {
  height: 80px;
}
.movie_genres ul {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}
.movie_genres ul li:nth-of-type(1):before {
  display: none;
}
.movie_genres ul li::before {
  content: "Â·";
  margin-right: 10px;
}
